


var getQuery ={
	getQueryString(name) { 
		  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //
          var r = window.location.search.substr(1).match(reg);  //
          if (r != null) return unescape(r[2]); return null; //
	  },
	 getJsonLength(jsonData){  
    	var jsonLength = 0;  
    	for(var item in jsonData){  
        	jsonLength++;  
    	}  
    	return jsonLength;  
	  }  	
    }
module.exports = getQuery


