/**
 * api
 */
import Vue from 'vue';

var API_ROOT = "http://106.14.135.243:8082/index/";


export const person = {
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * @returns 
   */
  getAddressList(data) {
    return Vue.http.get(API_ROOT + 'user/useraddresssel',{params:data});
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * address_name 
   * province 
   * city 
   * district 
   * address 
   * postcode 
   * mobile 
   * is_set_default 
   * @returns 
   */
  addressAdd(data) {
    return Vue.http.post(API_ROOT + 'user/useraddressadd',data);
  },
  /**
   * 
   * id
   * userId
   * token
   * address_name 
   * province 
   * city 
   * district 
   * address 
   * postcode 
   * mobile 
   * is_set_default 
   * @param {any} data 
   * @returns 
   */
  addressEdit(data) {
    return Vue.http.post(API_ROOT + 'user/useraddressedit',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token 
   * id 
   * @returns 
   */
  addressDefault(data) {
    return Vue.http.post(API_ROOT + 'user/defaultaddress',data);
  },
  /**
   * 
   * userId
   * token 
   * id 
   * @param {any} data 
   * @returns 
   */
  addressDelete(data) {
    return Vue.http.post(API_ROOT + 'user/useraddressdel',data);
  },
  /**
   * 
   * @param {any} data
   * userId
   * token 
   * is_default //1
   * id //id is_default0
   * @returns 
   */
  addressInfo(data) {
    return Vue.http.post(API_ROOT + 'user/useraddressinfo',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * pageSize
   * page
   * @returns 
   */
  shopCollectList(data) {
    return Vue.http.get(API_ROOT + 'user/usercollect',{params:data});
  },
  /**
   * userId
   * token
   * shopId
   * @param {any} data 
   * @returns 
   */
  collectShop(data) {
    return Vue.http.post(API_ROOT + 'user/usercollectadd',data);
  },
  /**
   * userId
   * token
   * shopId
   * @param {any} data 
   * @returns 
   */
  unCollectShop(data) {
    return Vue.http.post(API_ROOT + 'user/usercollectdel',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * realName
   * idCard
   * token
   * @returns 
   */
  userIdentify(data) {
    return Vue.http.post(API_ROOT + 'user/useridentiadd',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * content
   * contact
   * @returns 
   */
  feedback(data) {
    return Vue.http.post(API_ROOT + 'user/opinionAdd',data);
  }
}



