/**
 * 
 */
import Vue from 'vue';

var API_ROOT = "http://106.14.135.243:8082/index/";

export const login = {
  /**
   * 
   * @param {any} data 
   * user_phone
   * user_password 
   * @returns 
   */
  pwdLogin(data) {
    return Vue.http.post(API_ROOT + 'user/app_login', data);
  },
  /**
   * 
   * @param {any} data 
   * user_phone
   * code
   * user_password
   * @returns 
   */
  register(data){
    return Vue.http.post(API_ROOT + 'user/app_register',data);
  },
  /**
   * 
   * @param {any} data 
   * user_phone
   * code
   * user_password
   * @returns 
   */
  updatePwd(data){
    return Vue.http.post(API_ROOT + 'user/update_password',data);
  },
  /**
   * 
   * @param {any} data 
   * user_phone
   * type // 1 register: 2 back 3: logo: 4replace 
   * @returns 
   */
  sendCode(data){
    return Vue.http.post(API_ROOT + 'user/send_phone_code',data);
  },
  /**
   * 
   * @param {any} data 
   * user_phone
   * code
   * @returns 
   */
  codeLogin(data){
    return Vue.http.post(API_ROOT + 'user/code_login',data);
  }
}



