/**
 * 
 */

import Vue from 'vue';
var API_ROOT = "http://106.14.135.243:8082/index/";


export const order = {
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * cartId
   * @returns 
   */
  orderConfirm(data) {
    return Vue.http.post(API_ROOT + 'index/confirmorder',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * com
   * cn
   * @returns 
   */
  expressInfo(data) {
    return Vue.http.post(API_ROOT + 'user/alicloudapi',data);
  },
  /**
   * 
   * @param {any} data 
   * province
   * city
   * region
   * goodsId
   * @returns 
   */
  orderTpl(data) {
    return Vue.http.post(API_ROOT + 'index/tpl_list',data);
  },
  /**
   * 
   * @param {any} data 
   * tpl
   * province
   * city
   * region
   * goodsId
   * @returns 
   */
  tplPrice(data) {
    return Vue.http.post(API_ROOT + 'order/tpl_price',data);
  },
   /**
   * 
   * @param {any} data 
   * userId
   * token
   * goodsId
   * province
   * city
   * region
   * page_number
   * page
   * @returns 
   */
  goodsSince(data) {
    return Vue.http.get(API_ROOT + 'index/since_sel',{params:data});
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * goodsId
   * page_number
   * page
   * @returns 
   */
  goodsRevert(data) {
    return Vue.http.get(API_ROOT + 'index/revert_sel',{params:data});
  },
  /**
   * 
   * @param {any} data 
   * orderStatus [{"key":"orderStatus","value":"1","description":"0 123456 7"},{"key":"page_number","value":"10","description":"10"}]
   * userId
   * token
   * page_number
   * page
   * @returns 
   */
  orderlist(data) {
    return Vue.http.get(API_ROOT + 'order/orderlist',{params:data});
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * orderId
   * @returns 
   */
  orderDel(data) {
    return Vue.http.post(API_ROOT + 'order/orderdel',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * orderId
   * @returns 
   */
  orderReceipt(data) { //
    return Vue.http.post(API_ROOT + 'order/orderreceipt',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * orderId 
   * sinceId 
   * expressId 
   * logisticsName 
   * revertId 
   * company 
   * @returns 
   */
  orderReturn(data) {
    return Vue.http.post(API_ROOT + 'order/orderreturn',data);
  },
 /**
   * 
   * @param {any} data 
   * userId
   * token
   * orderId 
   * @returns 
   */
  orderSettle(data) {
    return Vue.http.post(API_ROOT + 'order/ordersettlement',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * orderId 
   * @returns 
   */
  orderInfo(data) {
    return Vue.http.post(API_ROOT + 'order/orderfind',data);
  },
   /**
   * 
   * @param {any} data 
   * "addressId": "46",//id 
   * "cartId": "3106", //id
   * "cart_tpl": 1,//
   * "freight_continued_price": "0.00",//
   * "freight_door_cost": "0.00",//
   * "freight_first_price": "0.00",//
   * "goodsAmount": 1,//
   * "order_time_number": 5,//
   * "rentTime": "1505562717",//
   * "rent_period_id": "13505",// id
   * "returnTime": "1505908317",//
   * "sinceId": ""//id 0 
   * @returns 
   */
  orderPay(data) {
    return Vue.http.post(API_ROOT + 'order/orderadd',data);
  },
  /**
   * 
   * @param {any} data
   * userId
   * token
   * orderId 
   * @returns 
   */
  orderShipPay(data) {
    return Vue.http.post(API_ROOT + 'order/ordershippingpay',data);
  },
  /**
   * 
   * @param {any} data 
   * userId
   * token
   * @returns 
   */
  orderNum(data) {
    return Vue.http.post(API_ROOT + 'Order/orderInfoto',data);
  },
  /**
   * 
   * @param {any} data
   * userId
   * token
   * orderId 
   * @returns 
   */
  orderSettleInfo(data) {
    return Vue.http.post(API_ROOT + 'order/setbill',data);
  },
  /**
   * 
   * userId
   * token
   * orderId 
   * @param {any} data 
   * content
   * @returns 
   */
  orderComment(data) {
    return Vue.http.post(API_ROOT + 'Order/orderComment',data);
  },
  /**
   * 
   * userId
   * token
   * orderId 
   * @param {any} data 
   * @returns 
   */
  myOrderComment(data) {
    return Vue.http.post(API_ROOT + 'order/mycomment',data);
  },
}



